import { Component, OnInit } from '@angular/core';
import { ImageDirectory } from 'src/app/directory/image.directory';

@Component({
	selector: 'app-introduction',
	templateUrl: './introduction.component.html',
	styleUrls: ['./introduction.component.scss']
})
export class IntroductionComponent implements OnInit {
	wordList: string[] = ['Learner', 'Explorer', 'Developer'];
	currentWord: string = '';
	currentIndex = 0;
	currentWordLength: number = 1;
	currentState = 'add';
	delay = 500;
	bg = ImageDirectory.BG;
	bgMobile = ImageDirectory.BGMOBILE;
	explore = ImageDirectory.EXPLORE;


	constructor() { }

	ngOnInit() {
		this.typeWord();
	}

	addLetter() {
		this.currentWord = this.wordList[this.currentIndex].slice(0, this.currentWordLength);
		this.currentWordLength += 1;
		if (this.currentWordLength > this.wordList[this.currentIndex].length) {
			this.currentState = "remove";
			this.delay = 1000;
		}
	}

	removeLetter() {
		this.currentWord = this.wordList[this.currentIndex].slice(0, this.currentWordLength);
		this.currentWordLength -= 1;
		if (this.currentWordLength < 0) {
			this.currentIndex = this.currentIndex === this.wordList.length - 1 ? 0 : this.currentIndex + 1;
			this.currentWordLength = 1;
			this.currentState = "add";
		}
	}

	typeWord() {
		this.delay = 500;
		if (this.currentWordLength <= this.wordList[this.currentIndex].length && this.currentState === 'add') {
			this.addLetter();
		}
		else if (this.currentWordLength >= 0 && this.currentState === 'remove') {
			this.removeLetter();
		}
		setTimeout(this.typeWord.bind(this), this.delay)

	}

}
