import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import * as $ from 'jquery';
import { ImageDirectory } from 'src/app/directory/image.directory';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {

  rightArrow = ImageDirectory.RIGHT_ARROW;
  leftArrow = ImageDirectory.LEFT_ARROW;

  @ViewChild('carousels', {static: true}) carousels: ElementRef;
  exploreList = [
    {
      'img': 'https://i1.sndcdn.com/artworks-000165384395-rhrjdn-t500x500.jpg',
      'index': -3
    },
    {
      'img': 'https://i1.sndcdn.com/artworks-000185743981-tuesoj-t500x500.jpg',
      'index': -2
    },
    {
      'img': 'https://i1.sndcdn.com/artworks-000158708482-k160g1-t500x500.jpg',
      'index': -1
    },
    {
      'img': 'https://i1.sndcdn.com/artworks-000062423439-lf7ll2-t500x500.jpg',
      'index': 0
    },
    {
      'img': 'https://i1.sndcdn.com/artworks-000028787381-1vad7y-t500x500.jpg',
      'index': 1
    },
    {
      'img': 'https://i1.sndcdn.com/artworks-000108468163-dp0b6y-t500x500.jpg',
      'index': 2
    },
    {
      'img': 'https://i1.sndcdn.com/artworks-000064920701-xrez5z-t500x500.jpg',
      'index': 3
    }
  ];
  moving: boolean = false;
  mouseLastPosition: number = 0;

  constructor() {}

  ngOnInit() {}

  next() {
    if(this.exploreList[this.exploreList.length - 1]['index'] > 0) {
      this.exploreList.forEach((element) => {
        element.index -= 1;
      });
    }
  }

  prev() {
    if(this.exploreList[0]['index'] < 0) {
      this.exploreList.forEach((element) => {
        element.index += 1;
      });
    }
  }
  
  
  
}
